import React from 'react';
import styled from 'styled-components';

const defaultSize = 20;

const Check = styled.span`
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: ${defaultSize}px;
  width: ${defaultSize}px;
  border: 2px solid ${({ theme }) => theme.checkbox.default.checkBorderColor};
  background: ${({ theme }) => theme.checkbox.default.checkBackground};
  border-radius: 50%;
  transition: all 0.15s ease;

  svg {
    position: relative;
    top: -1px;
    left: 0;
    height: 24px;
    width: 24px;
    overflow: initial;
    opacity: 0;
    transition: all 0.15s ease;
  }
`;

const HiddenCheckbox = styled.input.attrs({ type: 'checkbox' })`
  // Hide checkbox visually but remain accessible to screen readers.
  // Source: https://polished.js.org/docs/#hidevisually
  position: absolute;
  margin: -1px;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  overflow: hidden;
`;

const Label = styled.span`
  padding: 2px 6px 0 8px;
  font-family: ${({ theme }) => theme.fonts.primary};
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0.025em;
  color: ${({ theme }) => theme.checkbox.default.color};
`;

const Style = styled.label`
  position: relative;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 18px 6px;
  height: ${defaultSize}px;
  border-radius: 1000px;
  cursor: pointer;
  user-select: none;
  transition: all 0.15s ease;

  ${Check} {
    svg {
      path {
        stroke: ${({ theme }) => theme.checkbox.default.tickColor};
      }
    }
  }

  ${(props) =>
    !props.checked &&
    `
    &:hover {
      background: ${props.theme.checkbox.hover.background};
      
      ${Label} {
        color: ${props.theme.checkbox.hover.color};
      }

      ${Check} {
        border-color: ${props.theme.checkbox.hover.checkBorderColor};
      }
    }
  `};

  ${(props) =>
    props.checked &&
    `
    background: ${props.theme.checkbox.checked.background};

    ${Check} {
      border-color: ${props.theme.checkbox.checked.checkBorderColor};
      background: ${props.theme.checkbox.checked.checkBackground};

      svg {
        opacity: 1;

        path {
          stroke: ${props.theme.checkbox.checked.tickColor};
        }
      }
    }
  `};
`;

const Checkbox = ({ onChange, name, label, checked }) => (
  <Style htmlFor={name} checked={checked}>
    <HiddenCheckbox
      id={name}
      name={name}
      onChange={onChange}
      checked={checked}
    />
    <Check>
      <svg>
        <path
          d="M6.448,13.303l3.48,3.484l7.6-7.489"
          fill="transparent"
          stroke="#ffffff"
          strokeWidth="1.5"
          strokeLinejoin="square"
          strokeLinecap="square"
        />
      </svg>
    </Check>
    <Label>{label}</Label>
  </Style>
);

export default Checkbox;
