import React, { useCallback, useState } from 'react';
import styled from 'styled-components';
import filters from './filters';
import Button from './components/Button';
import Input from './components/Input';
import Checkbox from './components/Checkbox';

const Style = styled.div`
  width: 80%;

  @media screen and (min-width: 480px) {
    width: 380px;
  }
`;

const Filters = styled.ul`
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  margin-bottom: 10px;
`;

const Filter = styled.li`
  display: flex;
  align-items: center;
  margin-bottom: 8px;
`;

const Search = styled.div`
  margin-bottom: 30px;
`;

const Footer = styled.div`
  padding: 12px 0;
`;

const SearchFilter = ({ placeholder, startLength }) => {
  const [isMax, setIsMax] = useState(startLength);
  const [items, setItems] = useState(filters);
  const [checkboxItems, setCheckbox] = useState(new Map());
  const [inputLength, setInputLength] = useState(0);

  const handleInputChange = (e) => {
    setItems(
      filters.filter(
        (item) =>
          item.name.search(
            e.target.value.toLowerCase().replace(/\s+/g, '-')
          ) !== -1
      )
    );
    setInputLength(e.target.value.length);
  };

  const handleCheckboxChange = useCallback(({ target: { name, checked } }) => {
    setCheckbox((prevState) => {
      return new Map(prevState).set(name, checked);
    });
  }, []);

  const expandAll = () => {
    setIsMax(isMax === startLength ? filters.length : startLength);
  };

  return (
    <Style>
      <Search>
        <Input
          type="text"
          placeholder={placeholder}
          onChange={(e) => handleInputChange(e)}
          spellCheck={false}
        />
      </Search>
      <Filters>
        {items.slice(0, isMax).map(({ key, name, label }) => (
          <Filter key={key}>
            <Checkbox
              name={name}
              label={label}
              checked={checkboxItems.get(name)}
              onChange={(e) => handleCheckboxChange(e)}
            />
          </Filter>
        ))}
      </Filters>

      {inputLength < 2 && (
        <Footer>
          <Button onClick={() => expandAll()}>
            {isMax === startLength ? 'Show more' : 'Show less'}
          </Button>
        </Footer>
      )}
    </Style>
  );
};

export default SearchFilter;
